@mixin mobile{
  @media screen and  (max-width: 480px){
    @content;
    
  }
}
@mixin tablet{
  @media screen and  (max-width: 768px){
    @content;
    
  }
}
@mixin laptop{
  @media screen and  (max-width: 1200px){
    @content;
  }
}






.formcontaier {
  background-color: #6c95be;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWraper {
    background-color: white;
    padding: 20px 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;

    .logo {
      color: black;
      font-size: xx-large;
      font-weight: bold;
    }
    .title {
      color: black;
      font-size: 15px;
      font-weight: bold;
    }
    form {
      display: flex;
      flex-direction: column;
      gap: 15px;

      input {
        padding: 15px;

        &::placeholder {
          font-weight: bold;
        }
      }
      button {
        background-color: #4378d0;
        border: none;
        border-radius: 5px;
        padding: 10px 20px;
        color: white;
        cursor: pointer;
        &:hover {
          background-color: #ffffff;
          color: #000;
          border: 1px solid #4378d0;
        }
      }
      label {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 10px;
        font-weight: bold;
        cursor: pointer;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}
.home {
  background-color: #6c95be;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .container {
    border: 1px solid white;
    border-radius: 10px;
    width: 65%;
    height: 80%;
    display: flex;
    overflow: hidden;
    @include tablet{
      width: 90%;
    }

    .sidebar {
      flex: 1;
      border-right: 1px solid white;
      background-color: #ffffff;
      position: relative;

      .navbar {
        display: flex;
        align-items: center;
        background-color: #000;
        height: 50px;
        padding: 10px;
        justify-content: space-between;
        color: #ffffff;

        .logo {
          font-weight: bold;
          font-size: x-large;
          @include tablet{
            display: none;
          }
        }

        .user {
          display: flex;
          gap: 10px;
          align-items: center;

          img {
            height: 30px;
            width: 30px;
            border-radius: 50%;
            object-fit: cover;
          }
          button {
            background-color: #4378d0;
            border: none;
            padding: 5px;
            color: white;
            cursor: pointer;
            @include tablet{
              position: absolute;
              bottom: 10px;
            }
            &:hover {
              background-color: #ffffff;
              color: #000;
            }
          }
        }
      }
      .search {
        border-bottom: 1px solid black;

        .searchform {
          display: flex;
          padding: 10px;
          input {
            background-color: #ffffff;
            width: 100%;
            border: none;
            outline: none;
            color: rgb(0, 0, 0);
            font-size: 18px;
            &::placeholder {
              font-family: "Poppins", sans-serif;
              font-weight: bold;
            }
          }
          .searchicon{
            cursor: pointer;
          }
        }
      }
      .userchat {
        padding: 5px;
        display: flex;
        align-items: center;
        gap: 20px;
        color: #000000;
        cursor: pointer;
        &:hover {
          background-color: gray;
        }

        img {
          width: 50px;
          height: 50px;   
          border-radius: 50%;
        }
        .chatinfo {
          span {
            font-size: 18px;
            font-weight: 250;
          }
          p {
            font-size: 14px;
            color: rgb(85, 85, 85);
          }
        }
      }
    }
    .chat {
      flex: 2;

      .chatinfo {
        height: 50px;
        background-color: rgb(255, 255, 255);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
      }
      .chaticon {
        display: flex;
        gap: 10px;
        cursor: pointer;
      }

      .massages {
        background-color: antiquewhite;
        padding: 10px;
        height: calc(100% - 160px);
        overflow: scroll;

        .massage {
          display: flex;
          gap: 20px;
          margin-bottom: 20px;

          .massageinfo {
            display: flex;
            flex-direction: column;
            color: gray;
            font-weight: 300;

            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              object-fit: cover;
            }
          }
          .massagecontent {
            max-width: 80%;
            display: flex;
            flex-direction: column;
            gap: 10px;
            p {
              background-color: #ffffff;
              padding: 10px 20px;
              border-radius: 0px 10px 10px 10px;
              font-weight: 300;
              max-width: max-content;
            }
            img{
              width: 50%;
            }
          }
          &.owner{
            flex-direction: row-reverse;

            .massagecontent{
              align-items: flex-end;
            }

            p{
              background-color: #ffffff;
              padding: 10px 20px;
              border-radius: 10px 0px 10px 10px;
              font-weight: 300;
            }
          }
        }
      }
      .input {
        height: 50px;
        color: #000000;
        background-color: #efc6c6;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        input {
          background-color: #efc6c6;
          width: 100%;
          border: none;
          outline: none;
          color: rgb(0, 0, 0);
          font-size: 18px;

          &::placeholder {
            color: #000000;
          }
        }
        .send {
          display: flex;
          align-items: center;
          gap: 10px;

          img {
            height: 24px;
            cursor: pointer;
          }
          button {
            background-color: #4378d0;
            border: none;
            padding: 5px;
            color: white;
            cursor: pointer;
            &:hover {
              background-color: #ffffff;
              color: #000;
            }
          }
        }
      }
    }
  }
}
